import React, { useState, useEffect } from "react";
import { MDBProgress } from "mdbreact";

const ProgressB = ({average}) => {
  const [progrssing, setProgressing] = useState(0);

  useEffect(()=>{
      setTimeout(()=>{
          setProgressing(50)
      },500)
      setTimeout(()=>{
          setProgressing(average)
      },1000)
 
  },[average])

  return <MDBProgress  value={progrssing} className="my-2" color="blue-grey" />;
};

export default ProgressB