import React, { useState } from "react";
import {
  MDBNavbar,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavbarBrand,
} from "mdbreact";
import "./navbar.scss";

const Head = () => {
  const [toggole, setToggle] = useState(false);

  return (
    <MDBNavbar
      dark
      expand="md"
      fixed="top"
      className=" navBlack"
    >
    <MDBNavbarBrand>
    <strong>Ragheb Ben Amor</strong>
    </MDBNavbarBrand>
      <MDBNavbarToggler
        onClick={() => {
          setToggle(!toggole);
        }}
      />
      <MDBCollapse id="navbarCollapse3" isOpen={toggole} navbar>
        <MDBNavbarNav right>
          <MDBNavItem>
            <p>Main</p>
          </MDBNavItem>
        </MDBNavbarNav>
      </MDBCollapse>
    </MDBNavbar>
  );
};

export default Head;
