import React, { useState } from "react";
import uuid from "uuid";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome"
import {faGithub} from "@fortawesome/free-brands-svg-icons"


export default function Devproject({
  image,
  name,
  description,
  tools,
  year,
  github,
  link
}) {
  const [Show, setShow] = useState(false);

  return (
    <div className="devProject">
      <div
        key={uuid()}
        className="project"
        onMouseEnter={() => {
          setShow(true);
        }}
        onMouseLeave={() => {
          setShow(false);
        }}
      >
        <img
          src={image}
          alt={name}
          style={{ filter: Show ? "blur(8px)" : null }}
        />
        {Show ? (
          <a className="ProjectInfo" href={link}>
            <div>
              <p>Project Name:</p> <p>{name}</p>
            </div>
            <div>
              <p>Description:</p> <p>{description}</p>
            </div>
            <div>
              <p>Tools:</p> <p>{tools}</p>
            </div>
            <div>
              <p>Year:</p> <p>{year}</p>
            </div>
            <div>
              <p>Github:</p>
              {github === "private" ? (
                <p>Private</p>
              ) : (
                <a href={github}><FontAwesomeIcon icon={faGithub} /> source Code</a>
              )}
            </div>
          </a>
        ) : null}
      </div>
    </div>
  );
}
