import React, { useState } from "react";
import { MDBContainer } from "mdbreact";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faLaptopCode,
  faObjectUngroup
} from "@fortawesome/free-solid-svg-icons";
import "./portfolio.scss";
import Devproject from "./devProject";
import { projectDetails } from "./projectDetails";
import DesingProject from "./desingProject";
import uuid from "uuid"


export default function Portfolio() {
  const [gategory, setGategory] = useState(false);

  return (
    <MDBContainer className="portoflio">
      <h1>Portfolio</h1>
      <div className="CategoryCont">
        <span
          onClick={() => {
            setGategory(false);
          }}
          style={{background: !gategory ? "#78909C" : null}}
        >
          <p>
            <FontAwesomeIcon icon={faLaptopCode} />
          </p>
        </span>
        <span
          onClick={() => {
            setGategory(true);
          }}
          style={{background: gategory ? "#78909C" : null}}
        >
          <p>
            <FontAwesomeIcon icon={faObjectUngroup} />
          </p>
        </span>
      </div>
      <div className="projectCont">
        {  !gategory ?
            projectDetails.map(
          ({ image, name, description, tools, year, github, link }) => (
            <Devproject
            key={uuid()}
              image={image}
              name={name}
              description={description}
              tools={tools}
              year={year}
              github={github}
              link={link}
            />
          )
        )
       :
       <DesingProject />
    }
      </div>
    </MDBContainer>
  );
}
