import React from "react";
import { MDBContainer } from "mdbreact";
import "./skills.scss";
import ProgressB from "./progress";
import { SkillsDt } from "./skills.details";
import uuid from "uuid"

export default function Skills() {
  return (
    <div className='skillsCont'>
    <p className='title'>Professional Skills</p>
    <MDBContainer className="skills">
      {SkillsDt.map(({ label, percentage }) => (
        <section key={uuid()}>
          <div className="skillsLabel">
            <p>{label.toUpperCase()}</p>
            <p>{percentage}%</p>
          </div>
          <ProgressB average={percentage} />
        </section>
      ))}
    </MDBContainer>
    </div>
  );
}
